import { MouseEvent } from 'react';
import video from '../../assets/videos/fr-2.mp4';
import './Hero.css';

function Hero() {
  const handleOnMouseOver = (e: MouseEvent<HTMLVideoElement>) => {
    e.currentTarget.play();
  };

  const handleOnMouseOut = (e: MouseEvent<HTMLVideoElement>) => {
    e.currentTarget.pause();
  };
  return (
    <section className="product__container">
      <figure className="product__video-wrap">
        <video autoPlay loop preload="none" controls onMouseOver={handleOnMouseOver} onMouseOut={handleOnMouseOut}>
          <source src={video} />
        </video>
      </figure>
    </section>
  );
}

export default Hero;
